<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">折损积分：</span>
            <Input v-model="breakageIntegral" placeholder="积分ID/积分名称" class="input-width"></Input>
          </div>
        </Col>
        <Col span="10">
          <div  class="input-item">
            <daterange :name="'折损收取日期'" ref="regTime"  @start-time="timeChange('start',$event)"  @end-time="timeChange('end',$event)"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border>
          <div slot="header">
             <div class="table-title">累计折损 {{allBreakage}} </div>
          </div>
      </Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        breakageIntegral:'',// 折损积分
        startTime:'',//开始时间
        endTime:'',// 结束时间
        allBreakage:'0',
        loading:false,
        /*table表格*/
        tableColumns: [
         {
              title: '积分ID',
              key: 'point_cv',
              align: 'center',
              width:150
            },
              {
                title: '积分名称',
                key: 'point_name',
                align: 'center',
              },
              {
                title: '折损数量',
                key: 'amount',
                align: 'center',
              },
              {
                title: '折损收取时间',
                key: 'clear_time',
                align: 'center',
              },
              {
                title: '关联业务单号',
                align: 'center',
                key: 'order_seq_no',
              }
        ]
      }
    },
    components: {
      daterange
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //查询数据
        //this.searchInfo();
      })
    },
    methods: {
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.breakageIntegral=''//
        this.$refs.regTime.resetTime()
        this.startTime='' //开始时间
        this.endTime='' //结束时间
      },
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          point_name:this.breakageIntegral,
          min_clear_time:this.startTime.length>0?this.startTime.replace(/-/g,''):'',
          max_clear_time:this.endTime.length>0?this.endTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        };
        let jsonData = protocol.getJsonParams(protocol.getAllClearUnfreeze, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
          this.allBreakage=data.sum_amount
        })
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="start"){
          this.startTime=time
        }else if(e=="end"){
          this.endTime=time
        }
      },
      //导出数据
      exportOk(){
        let objData={
          point_name:this.breakageIntegral,
          min_clear_time:this.startTime.length>0?this.startTime.replace(/-/g,''):'',
          max_clear_time:this.endTime.length>0?this.endTime.replace(/-/g,''):'',
        };
        let jsonData = protocol.getJsonParams(protocol.exportAllClearUnfreeze, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
  .table-title{
    background-color: #F8F8F9;
    height:47px;
    padding:0 20px;
    font-weight: 700;
    text-align: center;
  }
</style>



